<template>
    <div id="sidebar-left">
      <el-menu
        :collapse="collapse"
        class="el-menu-vertical-demo"
        background-color="#428BCA"
        text-color="#fff"
        active-text-color="#fff"
        unique-opened router>
        <template v-for="(item,index) in sidebarInfo">
            <el-submenu :index="item.index" :key="item.index" v-if="item.subNav">
              <template slot="title">
                <i :class="item.icon"></i>
                <span>{{item.title}}</span>
              </template>
              <el-menu-item-group>
                <!--<template slot="title">分组一</template>-->
                <el-menu-item :index="list.index" v-for="(list,index) in item.subNav" :key="list.index">{{list.title}}</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
            <el-menu-item :index="item.index" :key="item.index" v-else>
              <template>
                <i :class="item.icon"></i>
                <span slot="title">{{item.title}}</span>
              </template>
            </el-menu-item>
        </template>
      </el-menu>

    </div>
</template>

<script>
  import {mapState,mapActions} from 'vuex'
    export default {
        name: 'sidebar',
        data () {
            return {
              indexPath:[],//保存点击过的页面路由信息
              sidebarInfo:[//左侧导航条信息
                {
                  icon: 'el-icon-setting',
                  index: 'Recommand',
                  title: 'Demo'
                },
                                
              ]
            }
        },
      computed:{
        ...mapState({
          collapse:state=>state.collapse
        }),
//        collapse(){
//          return this.$store.state.collapse
//        },
      },
      methods:{

      },
    }
</script>
<style scoped>

</style>



