    </div>
  </header>
</template>
<script>
  import {mapState,mapActions} from 'vuex';
    export default {
        name: 'Head',
        data () {
            return {              
            }
        },
      mounted(){

      },
      computed: {
        ...mapState({
          companyValue:state=>state.companyValue,
        }),
      },
      methods:{
        ...mapActions(["getCollapse"]),//简写
      }
    }
</script>
